.clock {
  height: 256px;
  width: 256px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(180deg, #757575 0%, #1d1d1b 100%);
  box-shadow: inset 1px 1px 0 rgba(47, 47, 80, 0.2);
  border: 2px solid #000000;
}

.clock__area {
  position: relative;
  height: $size-clock-area;
  width: $size-clock-area;
  background-color: #ffffff;
  border-radius: 50%;
  border: 2px solid #000000;

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    height: 185px;
    width: 185px;
    border-radius: 50%;
    z-index: 2;
  }

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    height: 200px;
    width: 200px;
    border-radius: 50%;
    z-index: 1;
  }
}

.clock__hand-cap {
  display: block;
  border-radius: 50%;
  width: 13px;
  height: 13px;
  background: linear-gradient(180deg, #ffffff 0%, #bfbfbf 100%);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.clock__hand {
  position: absolute;
  left: 50%;
  z-index: 1;
  border-radius: 1px;

  &--minutes {
    width: 6px;
    height: 91px;
    background-color: #050606;
    top: calc(50% - 30px);
    transform-origin: center calc(50% + 30px);
    transform: translate(-50%, -50%) rotate(20deg);
  }

  &--seconds {
    top: calc(50% - 35px);
    transform-origin: center calc(50% + 35px);
    transform: translate(-50%, -50%) rotate(0);
    background-color: #E9343F;
    width: 3px;
    height: 102px;
    animation-duration: 60s;
    animation-name: rotate;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }

  &--hours {
    width: 6px;
    height: 71px;
    background-color: #050606;
    top: calc(50% - 20px);
    transform-origin: center calc(50% + 30px);
    transform: translate(-50%, -50%) rotate(-20deg);
  }
}

.clock__details {
  position: relative;
  height: 100%;
  z-index: 3;
}

.clock__numbers {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
  display: flex;
  padding-left: 0;
  margin-bottom: 0;
  margin-top: 0;
}

.clock__number {
  position: absolute;
  list-style: none;
  line-height: normal;
  top: 50%;
  left: 50%;

  &:nth-child(1) {
    transform: translate(-12px, -94px);
  }

  &:nth-child(2) {
    transform: translate(38px, -85px);
  }

  &:nth-child(3) {
    transform: translate(68px, -55px);
  }

  &:nth-child(4) {
    transform: translate(77px, -13px);
  }

  &:nth-child(5) {
    transform: translate(65px, 30px);
  }

  &:nth-child(6) {
    transform: translate(35px, 57px);
  }

  &:nth-child(7) {
    transform: translate(-7px, 70px);
  }

  &:nth-child(8) {
    transform: translate(-47px, 58px);
  }

  &:nth-child(9) {
    transform: translate(-79px, 26px);
  }

  &:nth-child(10) {
    transform: translate(-87px, -12px);
  }

  &:nth-child(11) {
    transform: translate(-80px, -53px);
  }

  &:nth-child(12) {
    transform: translate(-49px, -83px);
  }
}

.clock__numerals {
  display: block;
  position: absolute;
  background-color: #1a1917;
  transform-origin: 0 center;
  left: 50%;
  margin-top: 3px;

  &--big {
    width: 3px;
    height: calc(100% - 6px);
    z-index: 2;
  }

  &--small {
    width: 1px;
    height: calc(100% - 6px);
  }

  &--3-9 {
    transform: rotate(90deg);
  }

  &--12-6 {
    transform: rotate(180deg);
  }

  &--2-8 {
    transform: rotate(240deg);
  }

  &--1-7 {
    transform: rotate(30deg);
  }

  &--4-10 {
    transform: rotate(120deg);
  }

  &--5-11 {
    transform: rotate(150deg);
  }

  &--1-31 {
    transform: rotate(6deg);
  }

  &--2-32 {
    transform: rotate(12deg);
  }

  &--3-33 {
    transform: rotate(18deg);
  }

  &--4-34 {
    transform: rotate(24deg);
  }

  &--6-36 {
    transform: rotate(36deg);
  }

  &--7-37 {
    transform: rotate(42deg);
  }

  &--8-38 {
    transform: rotate(48deg);
  }

  &--9-39 {
    transform: rotate(54deg);
  }
  
  &--11-41 {
    transform: rotate(66deg);
  }

  &--12-42 {
    transform: rotate(72deg);
  }

  &--13-43 {
    transform: rotate(78deg);
  }

  &--14-44 {
    transform: rotate(84deg);
  }

  &--16-46 {
    transform: rotate(96deg);
  }

  &--17-47 {
    transform: rotate(102deg);
  }

  &--18-48 {
    transform: rotate(108deg);
  }

  &--19-49 {
    transform: rotate(114deg);
  }

  &--21-51 {
    transform: rotate(126deg);
  }

  &--22-52 {
    transform: rotate(132deg);
  }

  &--23-53 {
    transform: rotate(138deg);
  }

  &--24-54 {
    transform: rotate(144deg);
  }

  &--26-56 {
    transform: rotate(156deg);
  }

  &--27-57 {
    transform: rotate(162deg);
  }

  &--28-58 {
    transform: rotate(168deg);
  }

  &--29-59 {
    transform: rotate(174deg);
  }
}

@keyframes rotate {
  from {
    transform: translate(-50%, -50%) rotate(0);
  }

  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}